.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: clamp(25rem, 19.118rem + 29.412vw, 37.5rem);
    -webkit-box-shadow: 0px -81px 30px 0px #111111 inset;
    -moz-box-shadow: 0px -81px 30px 0px #111111 inset;
    box-shadow: 0px -81px 30px 0px #111111 inset;
}

.bgImg {
    top: 0;
    height: clamp(25rem, 19.118rem + 29.412vw, 37.5rem);
    position: absolute;
    width: 100%;
    filter: brightness(0.3);
    z-index: -1;
    animation: 0.7s ease bgImgAnim;
}

@keyframes bgImgAnim {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

